"use client";

import { useState } from "react";
import { mockDemons } from "@/data/demons";
import { submitRecordAction } from "../actions"; // Import the action
export default function SubmitRecord() {
const [formData, setFormData] = useState({ playerName: "", demonId: "", videoUrl: "" });
  const [isSubmitting, setIsSubmitting] = useState(false);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsSubmitting(true);

    const result = await submitRecordAction({
      playerName: formData.playerName,
      demonId: parseInt(formData.demonId),
      videoUrl: formData.videoUrl
    });

    if (result.success) {
      alert("Record saved to db.json!");
      setFormData({ playerName: "", demonId: "", videoUrl: "" });
    }
    setIsSubmitting(false);
  };

  return (
    <main className="min-h-screen bg-black text-white p-8 font-sans">
      <div className="max-w-xl mx-auto">
        <h1 className="text-4xl font-black italic uppercase mb-2">Submit <span className="text-blue-600">Record</span></h1>
        <p className="text-zinc-500 text-sm mb-8">Submit your completion or high-score for verification.</p>

        <form onSubmit={handleSubmit} className="space-y-6 bg-zinc-900/50 p-8 rounded-3xl border border-zinc-800">
          {/* PLAYER NAME */}
          <div>
            <label className="block text-[10px] font-black uppercase text-zinc-500 mb-2 tracking-widest">Player Name</label>
            <input 
              required
              type="text"
              value={formData.playerName}
              onChange={(e) => setFormData({...formData, playerName: e.target.value})}
              className="w-full bg-black border border-zinc-800 rounded-xl p-3 outline-none focus:ring-2 focus:ring-blue-600 transition-all"
              placeholder="e.g. Doggie"
            />
          </div>

          {/* DEMON SELECTION */}
          <div>
            <label className="block text-[10px] font-black uppercase text-zinc-500 mb-2 tracking-widest">Select Demon</label>
            <select 
              required
              value={formData.demonId}
              onChange={(e) => setFormData({...formData, demonId: e.target.value})}
              className="w-full bg-black border border-zinc-800 rounded-xl p-3 outline-none focus:ring-2 focus:ring-blue-600 transition-all text-zinc-400"
            >
              <option value="">Choose a level...</option>
              {mockDemons.map(d => (
                <option key={d.id} value={d.id}>#{d.position} - {d.name}</option>
              ))}
            </select>
          </div>

          {/* VIDEO URL */}
          <div>
            <label className="block text-[10px] font-black uppercase text-zinc-500 mb-2 tracking-widest">YouTube Video URL</label>
            <input 
              required
              type="url"
              value={formData.videoUrl}
              onChange={(e) => setFormData({...formData, videoUrl: e.target.value})}
              className="w-full bg-black border border-zinc-800 rounded-xl p-3 outline-none focus:ring-2 focus:ring-blue-600 transition-all"
              placeholder="https://www.youtube.com/watch?v=..."
            />
          </div>

          <button 
            disabled={isSubmitting}
            type="submit"
            className="w-full py-4 bg-blue-600 hover:bg-blue-500 disabled:bg-zinc-800 rounded-xl font-black uppercase tracking-tighter transition-all shadow-lg shadow-blue-900/20"
          >
            {isSubmitting ? "Processing..." : "Submit to Moderators"}
          </button>

          {message && <p className="text-center text-green-500 font-bold text-sm mt-4 animate-bounce">{message}</p>}
        </form>
      </div>
    </main>
  );
}