"use server";

import fs from "fs/promises";
import path from "path";
import { revalidatePath } from "next/cache";

const dbPath = path.join(process.cwd(), "src/data/db.json");

export async function submitRecordAction(formData: { playerName: string; demonId: number; videoUrl: string }) {
  // 1. Read existing data
  const fileData = await fs.readFile(dbPath, "utf-8");
  const db = JSON.parse(fileData);

  // 2. Add new record
  const newRecord = {
    ...formData,
    id: Date.now(),
    status: "Pending"
  };

  db.records.push(newRecord);

  // 3. Write back to the file
  await fs.writeFile(dbPath, JSON.stringify(db, null, 2));

  // 4. Refresh the pages so the new record appears instantly
  revalidatePath("/list/[id]", "page");
  
  return { success: true };
}